import React, { useState, useEffect } from 'react';
import Page from 'components/LayoutComponents/Page';
import { getStudentByCode } from 'services/student';
import { Card, Spin, message } from 'antd';
import { Helmet } from 'react-helmet';
import Profile from './Profile';
import SearchBar from './SearchBar';
import './style.scss';

const StudentResume = () => {
  const [studentCode, setStudentCode] = useState('');
  const [hide, setHide] = useState(false);
  const [student, setStudent] = useState({});
  const [loading, setLoading] = useState(false);

  const getStudentInformation = async () => {
    if (!studentCode || studentCode === '') {
      setHide(true);
      return;
    }
    setLoading(true);
    try {
      const res = await getStudentByCode(studentCode);
      const newStudent = { ...res.data };
      setStudent(newStudent);
      setHide(false);
    } catch (err) {
      message.error(`Không tìm thấy sinh viên với mã ${studentCode}`);
      setHide(true);
    }
    setLoading(false);
  };

  useEffect(() => {
    getStudentInformation();
  }, [studentCode]);

  return (
    <Card title="Lý lịch sinh viên" id="student-resume">
      <Helmet title="Lý lịch sinh viên" />

      <SearchBar setStudentCode={setStudentCode} />

      <Spin spinning={loading}>
        <div id="profile" style={!hide ? {} : { display: 'none' }}>
          <Profile student={student} />
        </div>
      </Spin>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <StudentResume />
  </Page>
);



